export default {
	name: 'shear',
	props: {
		data: {
			type: Object, 
		}, 
		show:{
			type: Boolean, 
		}
	},
	data() {
		return { 
			fxtablist: [{
					id: 0,
					image: '/static/img/invite/icon-wx.png',
					name: '分享到微信'
				}, 






















			],
			fx: ''
		}
	},
	methods: {
		shearqita(e){
			let that=this;
			var index=e.currentTarget.dataset.index;
			var provider="";
			var scene="";
			var data="";
			var fx=that.fx;
			if(index==0){
				uni.share({
					provider: 'weixin',
					type: 5,
					imageUrl:fx.imageUrl,
					title: fx.title,
					miniProgram: {
						id: 'gh_e229d21f070b',
						path:fx.path, 
					},
					success(res) {
						resolve(res);
					},
					fail(err) {
					  var i={
						  code:1,
						  err
					  }
					  resolve(i);
					  // console.log("分享失败：",err);
					}
				});
			}else if(index==1){
				provider="weixin"; 
				scene= "WXSenceTimeline";
			} else if(index==2){
				provider="sinaweibo";
			}else if(index==3){ 
				provider="qq";
			}
			if(index!=4){
				uni.share({
				    provider:provider,
				    scene: scene, 
				    title: fx.name,
					href:fx.href,
					imageUrl:fx.imageUrl,
				    success: function (res) {
				        // console.log("success:" + JSON.stringify(res));
				    },
				    fail: function (err) {
				        // console.log("fail:" + JSON.stringify(err));
				    }
				}); 
			}else{
				uni.showModal({
					title:"提示",
					content:"是否要复制推荐码？", 
					success(res) {
						if(res.confirm){
							var text=String(that.userinfo.recode);
							uni.setClipboardData({
								data: text,
								success: function (res1) {
									// console.log(res1); 
									// that.$alert(res1);
								}
							});
						}
					}
				}) 
			}
		},
		c_show(){
			that.show=false
		} 
	},
	
}
